"use client";

import React, { useState, useMemo } from "react";
import Head from 'next/head';
import {
  Calculator, Zap, ShieldCheck, TrendingUp, Search,
  Target, Award, History, BookOpen, ChevronRight, Scale, Layers,
  Info, CheckCircle2, AlertCircle, ArrowUpRight, Sparkles, 
  BarChart3, Receipt, IndianRupee, Activity
} from 'lucide-react';

// --- Sub-Calculator Imports ---
import SIPCalculator from "../../Calculators/SubCalculators/SIPCalculator";
import LumpsumCalculator from "../../Calculators/SubCalculators/LumpsumCalculator";
import GoalCalculator from "../../Calculators/SubCalculators/GoalCalculator";
import RetirementCalculator from "../../Calculators/SubCalculators/RetirementCalculator";
import EMICalculator from "../../Calculators/SubCalculators/EMICalculator";
import EPFCalculator from "../../Calculators/SubCalculators/EPFCalculator";
import FDCalculator from "../../Calculators/SubCalculators/FDCalculator";
import TaxFDCalculator from "../../Calculators/SubCalculators/TaxFDCalculator";
import ReverseSIPCalculator from "../../Calculators/SubCalculators/ReverseSIPCalculator";
import CAGRCalculator from "../../Calculators/SubCalculators/CAGRCalculator";
import GoldInvestmentCalculator from "../../Calculators/SubCalculators/GoldInvestmentCalculator";
import GsecBondCalculator from "../../Calculators/SubCalculators/GsecBondCalculator";
import PPFCalculator from "../../Calculators/SubCalculators/PPFCalculator";
// Assume this is your new Brokerage sub-calculator
import BrokerageCalculator from "../../Calculators/SubCalculators/BrokerageCalculator";

// --- Comprehensive Data Hub ---
const contentData = {
  brokerage: {
    title: "Trade Net-Profit Analyzer",
    formula: "Net P&L = (Sell - Buy) - (Statutory Taxes + Brokerage)",
    variables: [
      { k: "STT", v: "Securities Transaction Tax (Sell Side)" },
      { k: "GST", v: "18% on Brokerage + Exchange Charges" },
      { k: "SEBI", v: "Flat turnover-based regulatory fee" }
    ],
    specs: [
      { feature: "Mode", value: "Equity / Intraday / F&O" },
      { feature: "Accuracy", value: "Includes Stamp Duty" },
      { feature: "Update", value: "Finance Act 2025 Ready" }
    ],
    logic: [
      { step: "Turnover Calc", desc: "Total value of Buy + Sell transactions." },
      { step: "Tax Layering", desc: "Applying slab-wise STT, SEBI fees, and GST." },
      { step: "Break-even Analysis", desc: "Finding the minimum price movement to cover all costs." }
    ],
    pros: ["Full transparency on 'In-hand' profit", "Avoid hidden tax surprises"],
    cons: ["STT varies by asset class", "State-wise Stamp Duty differs"],
    tips_tricks: ["For high-volume traders, choosing a flat-fee broker reduces the percentage-based impact on turnover."],
    insight: "Profit isn't just (Sell - Buy). Even a 'profitable' trade can be a net loss after accounting for the 5-6 layers of statutory taxes."
  },
  sip: {
    title: "SIP Wealth Projector",
    formula: "FV = P √ó ({[1 + i]^n ‚Äì 1} / i) √ó (1 + i)",
    variables: [
      { k: "P", v: "Monthly Installment" },
      { k: "i", v: "Periodic Rate (r/12/100)" },
      { k: "n", v: "Total Months" }
    ],
    specs: [
      { feature: "Strategy", value: "Rupee Cost Averaging" },
      { feature: "Tax Benefit", value: "ELSS 80C Support" },
      { feature: "Compounding", value: "Monthly Frequency" }
    ],
    logic: [
      { step: "Periodic Deployment", desc: "Fixed capital buys more units when prices fall." },
      { step: "Exponential Base", desc: "Reinvested returns build the principal base." }
    ],
    pros: ["Disciplined wealth creation", "Hedge against market timing"],
    cons: ["Market linked volatility"],
    tips_tricks: ["Start a 10% annual step-up to reach your goal 5 years earlier."],
    insight: "Time in the market is more valuable than timing the market."
  },
  // Default/Fallback
  default: {
    title: "Financial Logic Terminal",
    formula: "Result = f(P, R, T)",
    variables: [{ k: "V", v: "Variable Parameters" }],
    specs: [{ feature: "Processing", value: "Real-time" }],
    logic: [{ step: "Calculation", desc: "Mathematical modelling based on standard financial laws." }],
    pros: ["Data-driven decisions"],
    cons: ["Market conditions apply"],
    tips_tricks: ["Diversify assets to mitigate risk."]
  }
};

const CalculatorSection = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const [activeCalculator, setActiveCalculator] = useState("brokerage");

  const CALCULATOR_OPTIONS = useMemo(() => [
    { id: 'brokerage', label: 'Brokerage & Tax', icon: 'üßæ', category: 'Trading', component: BrokerageCalculator },
    { id: 'sip', label: 'SIP Engine', icon: 'üìà', category: 'Investment', component: SIPCalculator },
    { id: 'lumpsum', label: 'Lumpsum Pro', icon: 'üí∞', category: 'Investment', component: LumpsumCalculator },
    { id: 'goal', label: 'Goal Planner', icon: 'üéØ', category: 'Planning', component: GoalCalculator },
    { id: 'retirement', label: 'Retirement', icon: 'üèñÔ∏è', category: 'Planning', component: RetirementCalculator },
    { id: 'emi', label: 'Loan EMI', icon: 'üí≥', category: 'Banking', component: EMICalculator },
    { id: 'cagr', label: 'CAGR Tool', icon: 'üìä', category: 'Investment', component: CAGRCalculator },
    { id: 'ppf', label: 'PPF Safe', icon: 'üîí', category: 'Tax Saving', component: PPFCalculator },
    { id: 'gold', label: 'Gold Value', icon: 'ü•á', category: 'Investment', component: GoldInvestmentCalculator },
  ], []);

  const filteredOptions = CALCULATOR_OPTIONS.filter(opt => 
    opt.label.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const current = contentData[activeCalculator] || contentData.default;
  const ActiveComponent = CALCULATOR_OPTIONS.find(opt => opt.id === activeCalculator)?.component;

  return (
    <div className="bg-[#fdfdfe] dark:bg-[#020617] min-h-screen text-slate-900 dark:text-slate-100 transition-colors duration-500">
      <Head><title>{current.title} | Financial Intelligence</title></Head>

      <main className="max-w-[1400px] mx-auto px-4 py-6 md:py-10 grid lg:grid-cols-12 gap-8">
        
        {/* --- SIDEBAR --- */}
        <aside className="lg:col-span-3 space-y-6">
          <div className="sticky top-28">
            <div className="relative mb-8 group">
              <Search className="absolute left-4 top-1/2 -translate-y-1/2 text-slate-400 group-focus-within:text-blue-500 transition-colors" size={18} />
              <input 
                type="text" placeholder="Search engine..."
                className="w-full bg-white dark:bg-slate-900 border-2 border-slate-100 dark:border-slate-800 rounded-2xl py-4 pl-12 pr-4 text-xs font-bold focus:border-blue-500 outline-none transition-all shadow-sm"
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>

            <nav className="space-y-2 max-h-[70vh] overflow-y-auto no-scrollbar pr-2 pb-10">
              {filteredOptions.map((tab) => (
                <button
                  key={tab.id}
                  onClick={() => setActiveCalculator(tab.id)}
                  className={`flex items-center gap-4 px-5 py-4 rounded-2xl font-black text-[11px] uppercase tracking-widest transition-all duration-300 w-full border-2 ${
                    activeCalculator === tab.id
                      ? "bg-blue-600 text-white border-blue-600 shadow-xl shadow-blue-500/20 translate-x-1"
                      : "bg-white dark:bg-slate-900 text-slate-500 border-transparent hover:border-slate-100 dark:hover:border-slate-800"
                  }`}
                >
                  <span className="text-xl">{tab.icon}</span>
                  <div className="text-left">
                    <p className="leading-none mb-1">{tab.label}</p>
                    <span className={`text-[8px] font-medium opacity-60 ${activeCalculator === tab.id ? 'text-white' : 'text-slate-400'}`}>
                      {tab.category}
                    </span>
                  </div>
                </button>
              ))}
            </nav>
          </div>
        </aside>

        {/* --- MAIN INTERFACE --- */}
        <div className="lg:col-span-9 space-y-8">
          
          <header className="relative p-8 md:p-12 rounded-[3rem] bg-slate-900 text-white overflow-hidden shadow-2xl">
            <div className="relative z-10 flex flex-col md:flex-row justify-between items-start md:items-center gap-6">
              <div className="space-y-2">
                <div className="flex items-center gap-2 text-blue-400 text-[10px] font-black uppercase tracking-[0.4em]">
                  <Activity size={14} /> Intelligence Terminal
                </div>
                <h1 className="text-4xl md:text-5xl font-black tracking-tighter uppercase italic leading-none">
                  {current.title}
                </h1>
              </div>
              <div className="bg-white/10 backdrop-blur-md p-5 rounded-3xl border border-white/10">
                <p className="text-[10px] font-black opacity-50 uppercase mb-2">Net Realization Engine</p>
                <div className="flex items-center gap-3 text-emerald-400 font-black text-sm">
                  <IndianRupee size={18} /> FY 2025-26 UPDATED
                </div>
              </div>
            </div>
            <div className="absolute -right-20 -top-20 w-80 h-80 bg-blue-600/20 rounded-full blur-[100px]" />
          </header>

          <section className="bg-white dark:bg-slate-900 rounded-[3rem] p-4 md:p-10 shadow-xl border border-slate-100 dark:border-slate-800">
            {ActiveComponent && <ActiveComponent />}
          </section>

          {/* Mastering The Logic Visualization */}
          <div className="bg-gradient-to-br from-blue-600 to-indigo-700 rounded-[3rem] p-10 text-white shadow-xl relative overflow-hidden">
             <div className="grid md:grid-cols-2 gap-10 items-center relative z-10">
                <div className="space-y-6">
                   <h3 className="text-3xl font-black uppercase italic tracking-tighter">Mastering the Logic</h3>
                   <p className="text-blue-100 text-sm leading-relaxed font-medium">
                      {current.insight}
                   </p>
                   <div className="flex gap-4">
                      <div className="bg-white/10 px-4 py-2 rounded-xl text-[10px] font-black border border-white/20">VALIDATED MODEL</div>
                      <div className="bg-emerald-500/20 px-4 py-2 rounded-xl text-[10px] font-black border border-emerald-500/30 text-emerald-300">PRECISION 1.0</div>
                   </div>
                </div>
                <div className="bg-white/10 backdrop-blur-xl rounded-[2.5rem] p-8 border border-white/20 text-center">
                   <p className="text-[10px] font-black uppercase tracking-widest text-blue-200 mb-6">Concept Visualization</p>
                   <div className="flex justify-center items-end gap-2 h-24 mb-4">
                      {/* Animated visualization bars */}
                      <div className="w-8 bg-blue-400 rounded-t-lg h-full animate-bounce" style={{animationDelay: '0.1s'}} />
                      <div className="w-8 bg-blue-200 rounded-t-lg h-[60%] animate-bounce" style={{animationDelay: '0.2s'}} />
                      <div className="w-8 bg-emerald-400 rounded-t-lg h-[85%] animate-bounce" style={{animationDelay: '0.3s'}} />
                   </div>
                   <p className="text-[10px] font-bold text-blue-200 uppercase tracking-tighter italic">"Visualization: {activeCalculator === 'brokerage' ? 'Tax Decomposition' : 'Growth Curve'}"</p>
                </div>
             </div>
             <div className="absolute -left-10 -bottom-10 w-40 h-40 bg-white/5 rounded-full blur-3xl" />
          </div>

          
          {/* Technical Grid */}
          <div className="grid md:grid-cols-2 gap-8">
            {/* Specs Table */}
            <div className="bg-white dark:bg-slate-900 rounded-[2.5rem] border border-slate-100 dark:border-slate-800 shadow-xl overflow-hidden">
              <div className="bg-slate-50 dark:bg-slate-800/50 px-8 py-6 border-b dark:border-slate-800">
                <h3 className="text-[10px] font-black uppercase tracking-widest text-slate-500 flex items-center gap-2">
                  <Scale size={16} /> Regulatory Specifications
                </h3>
              </div>
              <div className="divide-y dark:divide-slate-800">
                {current.specs?.map((spec, idx) => (
                  <div key={idx} className="flex justify-between items-center px-8 py-5 hover:bg-blue-50/50 dark:hover:bg-blue-900/10 transition-colors">
                    <span className="text-[11px] font-black uppercase text-slate-400">{spec.feature}</span>
                    <span className="text-xs font-black text-slate-900 dark:text-white uppercase tracking-tighter">{spec.value}</span>
                  </div>
                ))}
              </div>
            </div>

            {/* Formula Block */}
            <div className="bg-slate-950 rounded-[2.5rem] p-8 text-white flex flex-col justify-between border border-slate-800 shadow-2xl relative">
              <div>
                <h3 className="text-[10px] font-black uppercase tracking-widest text-blue-500 mb-8 flex items-center gap-2">
                   <Zap size={14} className="fill-current" /> Computational Formula
                </h3>
                <div className="bg-white/5 p-6 rounded-3xl border border-white/10 text-center mb-8 group hover:bg-white/10 transition-colors">
                  <code className="text-lg md:text-xl font-mono font-bold text-blue-400 tracking-tighter block leading-relaxed">
                    {current.formula}
                  </code>
                </div>
              </div>
              <div className="space-y-4">
                {current.variables?.map((v, i) => (
                  <div key={i} className="flex justify-between items-center text-[10px] font-bold border-b border-white/5 pb-3">
                    <span className="text-blue-500 bg-blue-500/10 px-2 py-1 rounded-md">{v.k}</span>
                    <span className="text-slate-400 uppercase tracking-widest text-right">{v.v}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Strategic Roadmap Logic */}
          <div className="bg-white dark:bg-slate-900 rounded-[2.5rem] border border-slate-100 dark:border-slate-800 shadow-xl overflow-hidden">
             <div className="bg-slate-50 dark:bg-slate-800/50 px-8 py-6 border-b dark:border-slate-800 flex justify-between items-center">
                <h3 className="text-[10px] font-black uppercase tracking-widest text-slate-500 flex items-center gap-2">
                   <Receipt size={16} /> Operational Roadmap
                </h3>
             </div>
             <div className="p-8 grid md:grid-cols-3 gap-8">
                {current.logic?.map((l, i) => (
                   <div key={i} className="relative pl-10">
                      <span className="absolute left-0 top-0 text-3xl font-black text-blue-600/10 italic">0{i+1}</span>
                      <h4 className="text-[11px] font-black uppercase mb-2 text-slate-900 dark:text-white">{l.step}</h4>
                      <p className="text-xs text-slate-500 dark:text-slate-400 font-medium leading-relaxed">{l.desc}</p>
                   </div>
                ))}
             </div>
          </div>

          {/* Pros, Cons & Strategic Tips */}
          <div className="grid md:grid-cols-3 gap-6">
            <div className="bg-emerald-50 dark:bg-emerald-900/10 p-8 rounded-[2rem] border border-emerald-100 dark:border-emerald-900/20 shadow-sm">
              <h3 className="font-black text-[10px] uppercase mb-6 text-emerald-600 flex items-center gap-2">
                <TrendingUp size={14} /> Efficiency Gains
              </h3>
              <ul className="space-y-4">
                {current.pros?.map((p, i) => (
                  <li key={i} className="text-xs font-bold text-slate-700 dark:text-slate-300 flex items-start gap-2 leading-relaxed">
                    <span className="text-emerald-500">‚Ä¢</span> {p}
                  </li>
                ))}
              </ul>
            </div>

            <div className="bg-rose-50 dark:bg-rose-900/10 p-8 rounded-[2rem] border border-rose-100 dark:border-rose-900/20 shadow-sm">
              <h3 className="font-black text-[10px] uppercase mb-6 text-rose-600 flex items-center gap-2">
                <ShieldCheck size={14} /> Risk Factors
              </h3>
              <ul className="space-y-4">
                {current.cons?.map((c, i) => (
                  <li key={i} className="text-xs font-bold text-slate-700 dark:text-slate-300 flex items-start gap-2 leading-relaxed">
                    <span className="text-rose-500">‚Ä¢</span> {c}
                  </li>
                ))}
              </ul>
            </div>

            <div className="bg-slate-900 p-8 rounded-[2rem] text-white shadow-2xl relative group">
              <h3 className="font-black text-[10px] uppercase mb-6 text-blue-400 flex items-center gap-2">
                <BookOpen size={14} /> Strategic Insight
              </h3>
              <p className="text-sm font-bold leading-relaxed italic opacity-90 tracking-tight">
                "{current.tips_tricks?.[0]}"
              </p>
              <button className="mt-8 flex items-center gap-2 text-[10px] font-black uppercase tracking-[0.2em] text-blue-400 group-hover:text-white transition-colors">
                 Deep Dive Report <ArrowUpRight size={14} />
              </button>
            </div>
          </div>
        </div>
      </main>

      <footer className="max-w-7xl mx-auto px-4 py-10 border-t dark:border-slate-800 text-center">
        <p className="text-[10px] font-bold text-slate-400 uppercase tracking-[0.5em]">Financial Logic Terminal &copy; 2025 | All Tax Models Verified</p>
      </footer>

      <style jsx global>{`
        .no-scrollbar::-webkit-scrollbar { display: none; }
        .no-scrollbar { -ms-overflow-style: none; scrollbar-width: none; }
        ::selection { background: #2563eb; color: white; }
      `}</style>
    </div>
  );
};

export default CalculatorSection;